@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

li {
  list-style: none;
}

body {
  background: #EEEEEE;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}
